    (document.getElementById('language-select') as HTMLSelectElement).value = settings.language || 'en';
    (document.getElementById('show-valid') as HTMLInputElement).checked = settings.showValid;
    (document.getElementById('show-last') as HTMLInputElement).checked = settings.showLast;
    (document.getElementById('show-mobility') as HTMLInputElement).checked = settings.showMobility;
